import {Menu, MenuItem, Select} from "@material-ui/core";
import React, {useEffect} from "react";
import AppBar from '@material-ui/core/AppBar';
import Typography from "@material-ui/core/Typography";
import Toolbar from "@material-ui/core/Toolbar";
import i18next from "i18next";
import {useHistory, useLocation} from "react-router-dom";

import TranslateIcon from '@material-ui/icons/Translate';
import {getCurrentLanguage, getIsVerticalMobile, getStringAfter} from "../my_util/Util";
import Button from "@material-ui/core/Button";

function getCurrentLanguageDistrict() {
    let currLanguage = getCurrentLanguage();
    
    const hyphenIndex = currLanguage.indexOf('-');
    if (hyphenIndex !== -1) {
        currLanguage = currLanguage.substr(0, hyphenIndex);
    }
    return currLanguage;
}

interface LanguageEntry {
    language: string,
    languageName: string
}

const supportedLanguages: Array<LanguageEntry> = [
    {language: 'en', languageName: 'English'},
    {language: 'zh', languageName: '简体中文'},
]

function LanguageSelector(props: {}) {
    const currLanguageDistrict = getCurrentLanguageDistrict();
    
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null as null | HTMLElement);
    
    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };
    
    const handleClose = () => {
        setAnchorEl(null);
    };
    
    return <>
        <Button aria-controls="language-menu" aria-haspopup="true"
                onClick={handleClick}
                style={{color: 'white'}}>
            <TranslateIcon/>
            {getIsVerticalMobile() ? undefined :
                <Typography style={{marginLeft: 5, textTransform: 'none'}}>{
                    supportedLanguages.find(entry => entry.language === currLanguageDistrict)?.languageName
                }</Typography>}
        </Button>
        <Menu
            id="language-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
        >
            {supportedLanguages.map(entry =>
                <MenuItem value={entry.language} key={entry.language} onClick={() => {
                    i18next.changeLanguage(entry.language);
                    handleClose();
                }}>{entry.languageName}</MenuItem>)}
        </Menu>
        
        
        {/*<Select id='language-select' style={{*/}
        {/*    color: 'white'*/}
        {/*}} value={currLanguageDistrict} onChange={(a) => {*/}
        {/*    var languageStr = a.target.value;*/}
        {/*    i18next.changeLanguage(languageStr as string)*/}
        {/*}}>*/}
        {/*    <MenuItem value={'zh'}>简体中文</MenuItem>*/}
        {/*    <MenuItem value={'en'}>English</MenuItem>*/}
        {/*</Select>*/}
    </>
}

export function MyBanner(props: {
    text: string, color: string, children?: any,
    sticky?: boolean, pageIconPath: string
}) {
    const location = useLocation();
    
    let history = useHistory();
    
    
    // console.log(currLanguage)
    
    useEffect(() => {
        document.title = props.text;
        // @ts-ignore
        document.getElementById('page_icon').href = props.pageIconPath;
    }, []);
    
    return <AppBar style={{
        position: props.sticky ? 'sticky' : 'relative', backgroundColor: props.color,
        margin: 0, padding: 0,
        top: props.sticky ? 0 : undefined
    }}>
        <Toolbar>
            {props.children}
            <Button onClick={() => {
                const currPath = location.pathname;
                const i = currPath.lastIndexOf('/');
                if (i !== -1) {
                    history.push(currPath.substr(0, i));
                }
            }} style={{textTransform: 'none'}}>
                <Typography variant='h6' style={{color: 'white'}}>{props.text}</Typography>
            </Button>
            
            <div style={{
                flexGrow: 1
            }}/>
            
            <LanguageSelector/>
        </Toolbar>
    
    
    </AppBar>;
}