var allTypes = Object.keys(defaultRenderers);

type Content = Array<HeadingWithContent | Array<Content>>

interface HeadingWithContent {
    myType: 'heading_with_content',
    headingElement: any | null,//only the root has null heading
    contents: Array<HeadingWithContent | any>
}

function isHeading(node: any) {
    return node.type === 'heading';
}

function isHeadingWithContent(obj: any): obj is HeadingWithContent {
    return obj.myType === 'heading_with_content';
}

const toRemoveFromHash: RegExp = /[!"#$%&'()*+,./:;<=>?@\^`{|}~]/g

function headingTextToHash(heading: string) {
    return '#' + heading.toLowerCase().replace(/ /g, '-')
        .replace(toRemoveFromHash, '');
}

function produceHeadingHash(headingNode: any): string {
    // @ts-ignore
    const joined: string = headingNode.children.map(n => n.value).join('');
    
    const assignedHash = getStringBetween(joined, '{#', '}');
    if (assignedHash.length !== 0) {
        return headingTextToHash(assignedHash);
    }
    
    return headingTextToHash(joined);
}

class HeadingHierarchyParser {
    children: Array<any>;
    currIndex: number;
    headingDiscriminatorNumbers: Map<string, number>;
    
    constructor(c: Array<any>) {
        this.children = c;
        this.currIndex = 0;
        this.headingDiscriminatorNumbers = new Map();
    }
    
    isEnded(): boolean {
        return this.currIndex >= this.children.length;
    }
    
    parseNonHeadingContents(): Array<any> {
        const headingIndex = _.findIndexFrom(isHeading, this.currIndex, this.children);
        if (headingIndex === -1) {
            const result = this.children.slice(this.currIndex);
            this.currIndex = this.children.length;
            return result;
        }
        const result = this.children.slice(this.currIndex, headingIndex);
        this.currIndex = headingIndex;
        return result;
    }
    
    parseHierarchy(minDepth: number): Array<HeadingWithContent | any> {
        const base: Array<HeadingWithContent | any> = this.parseNonHeadingContents();
        
        while (!this.isEnded()) {
            const currHeading = this.children[this.currIndex];
            const currDepth = currHeading.depth;
            if (currDepth < minDepth) {
                return base;
            }
            this.currIndex += 1;//swallow the header
            
            const remaining = this.parseHierarchy(currDepth + 1);
            
            const originalHeadingHash = produceHeadingHash(currHeading);
            const number = this.headingDiscriminatorNumbers.get(originalHeadingHash);
            let discriminatedHeadingHash;
            if (number !== undefined) {
                this.headingDiscriminatorNumbers.set(originalHeadingHash, number + 1);
                discriminatedHeadingHash = originalHeadingHash + '-' + number;
            } else {
                this.headingDiscriminatorNumbers.set(originalHeadingHash, 1);
                discriminatedHeadingHash = originalHeadingHash;
            }
            currHeading.myHeadingHash = discriminatedHeadingHash;
            
            base.push({myType: 'heading_with_content', headingElement: currHeading, contents: remaining});
        }
        
        return base;
    }
    
    parseDocument(): HeadingWithContent {
        const contents = this.parseHierarchy(0);
        
        return {
            myType: 'heading_with_content',
            headingElement: null,//only the root has null heading
            contents: contents
        };
    }
}

function extractHeadingContentGroup(children: Array<any>): Array<HeadingWithContent | any> {
    
    var result = new HeadingHierarchyParser(children).parseHierarchy(0);
    
    
    return result;
}

function getRendererProps(props: ReactMarkdown.ReactMarkdownProps, tree: any): any {
    var renderers = xtend(defaultRenderers, props.renderers);
    
    var renderProps = xtend(props, {
        renderers: renderers,
        definitions: getDefinitions(tree)
    });
    return renderProps;
}

export async function myParseMarkdownAsync(props: ReactMarkdownProps) {
    var src = props.source || props.children || '';
    
    var processor = unified().use(parse).use(props.plugins || []); // eslint-disable-next-line no-sync
    
    var tree = await processor.run(processor.parse(src));
    
    tree = wrapTableRows(tree);
    
    tree = addListMetadata()(tree);
    
    tree = naiveHtml(tree);
    
    // console.log(tree)
    
    return tree;
}

