function RightBarSection(props: {
    outerSelectedHash: string, item: HeadingWithContent, layer: number,
    setOuterSelected: null | ((cond: boolean) => void),
    onTitleClick: () => void,
    onViewed: (termDom: HTMLDivElement) => void
}) {
    let [opened, setOpened] = useState(false);
    let [selected, setSelected] = useState(false);
    
    const location = useLocation();
    const history = useHistory()
    
    const item = props.item;
    
    const headingHash = item.headingElement.myHeadingHash;
    
    // console.log(headingHash);
    
    const hasContent = item.contents.findIndex(w => isHeadingWithContent(w)) !== -1;
    
    const domRef = useRef(null as (null | HTMLDivElement));
    
    useEffect(() => {
        const viewed = props.outerSelectedHash === headingHash;
        
        setSelected(viewed);
        
        if (props.setOuterSelected) {
            props.setOuterSelected(viewed);
        }
        
        if (viewed) {
            if (domRef.current) {
                props.onViewed(domRef.current);
            }
        }
    }, [headingHash, props.outerSelectedHash]);
    
    // @ts-ignore
    let toDisplay: string = item.headingElement.children.map(s => s.value).join(' ');
    
    // remove for example {#hash}
    toDisplay = removeAssignedHash(toDisplay);
    
    // cut if too long
    if (toDisplay.length > 60) {
        toDisplay = toDisplay.substr(0, 60) + '...';
    }
    
    const headingColor = getHeadingColor(item.headingElement.depth);
    
    return <React.Fragment key={headingHash}>
        <ListItem ref={domRef} button style={{
            padding: 3, paddingLeft: props.layer * 10 + 10,
            display: "flex", flexDirection: "row", alignItems: "stretch",
            backgroundColor: (location.hash === headingHash) ? headingSelectedColor : undefined
        }} selected={selected} onClick={() => {
            if (!(opened && hasContent)) {
                history.push(headingHash);
                
                props.onTitleClick();
            }
            
            setOpened(!opened);
        }}>
            <div style={{
                display: "inline-flex", width: 5, background: headingColor,
                marginTop: 1, marginBottom: 0, marginRight: 5,
                flexShrink: 0
            }}/>
            {toDisplay}
            {hasContent && (opened ? <ExpandMore/> : <ExpandLess/>)}
        </ListItem>
        {hasContent && <Collapse in={opened} timeout={200}>
            <List dense={true} style={{padding: 0}}>
                {item.contents.map((w, index) => {
                    if (!isHeadingWithContent(w)) {
                        return undefined;
                    }
                    return <RightBarSection
                        key={index}
                        outerSelectedHash={props.outerSelectedHash}
                        item={w}
                        layer={props.layer + 1}
                        setOuterSelected={(cond) => {
                            setSelected(cond);
                            
                            if (cond) {
                                setOpened(true);
                            }
                            
                            if (props.setOuterSelected) {
                                props.setOuterSelected(cond);
                            }
                        }}
                        onTitleClick={props.onTitleClick}
                        onViewed={props.onViewed}
                    />;
                })}
            </List>
        </Collapse>}
    </React.Fragment>;
}

interface HeadingRef {
    headingHash: string,// has '#'
    element: HTMLElement
}

function getHeadingRefs(result: Array<HeadingRef>, hierarchy: Array<any>) {
    // console.log(hierarchy);
    hierarchy.forEach((item) => {
        if (isHeadingWithContent(item)) {
            const headingHash = item.headingElement.myHeadingHash;
            const element = document.getElementById(headingHash);
            if (element !== null) {
                result.push({
                    headingHash: headingHash,
                    element: element
                });
            } else {
                console.error('cannot find ' + headingHash)
            }
            
            getHeadingRefs(result, item.contents)
        }
    })
}

// const MemoedWikiSection = React.memo(WikiSection);

function FlatWikiSection(props: { root: any, renderOptions: any }) {
    const isMatureRef = useRef(false);
    
    // console.log("flat")
    
    useEffect(() => {
        setTimeout(() => {
            isMatureRef.current = true;
        }, 1000);
    }, []);
    
    return <HeadingContext.Provider value={{isMatureRef: isMatureRef}}>
        {astToReact(props.root, props.renderOptions)}
    </HeadingContext.Provider>;
}

const MemoedFlatWikiSection = React.memo(FlatWikiSection);

const MemoedRightBarSection = React.memo(RightBarSection);

export function useWikiContent(
    contentUrl: string,
    renderers: any, plugins: any,
    scrollDomRef: MutableRefObject<DOMElement<any, any>>,
    fallbackUrl: string,
    rightBarScrollRef: MutableRefObject<null | HTMLDivElement>
): {
    wikiContentComponent: ReactElement,
    rightBarComponent: ReactElement,
    updateScrollFunc: () => void,
} {
    let reactMarkdownProps: ReactMarkdownProps = {
        renderers: renderers, plugins: plugins,
        children: 'loading markdown'
    };
    
    const [tree, setTree] = useState(null);
    
    const [errorLoading, setErrorLoading] = useState(false);
    
    const [isAwaitingTranslation, setIsAwaitingTranslation] = useState(false);
    
    const [rightBarEnabled, setRightBarEnabled] = useState(false);
    
    const hierarchyRef: MutableRefObject<null | Array<HeadingWithContent | any>> =
        useRef(null);
    
    const renderPropsRef = useRef(null);
    
    useEffect(() => {
        async function doParseWikiContent() {
            let markdownResult: AxiosResponse<any>;
            try {
                markdownResult = await Axios({
                    url: isAwaitingTranslation ? fallbackUrl : contentUrl
                });
            } catch (e) {
                setErrorLoading(true);
                return;
            }
            
            if (!isAwaitingTranslation) {
                if (markdownResult.data === "awaiting translation") {
                    setIsAwaitingTranslation(true);
                    return;
                }
            }
            
            let parsedMarkdown = await myParseMarkdownAsync({
                // @ts-ignore
                renderer: renderers, plugins: plugins,
                children: markdownResult.data
            });
            
            // @ts-ignore
            hierarchyRef.current = extractHeadingContentGroup(parsedMarkdown.children);
            
            renderPropsRef.current = getRendererProps(reactMarkdownProps, parsedMarkdown);
            
            // @ts-ignore
            setTree(parsedMarkdown);
            setErrorLoading(false);
            
            // console.log(parsedMarkdown)
        }
        
        doParseWikiContent()
        
        setTimeout(() => setRightBarEnabled(true), 1200);
    }, [contentUrl, isAwaitingTranslation]);
    
    const rendererProps: any = renderPropsRef.current;
    
    // @ts-ignore
    const hierarchy = hierarchyRef.current;
    
    const [viewingSectionHash, setViewingSectionHash] = useState('');
    
    const wikiContents = hierarchy === null ? undefined :
        <MemoedFlatWikiSection root={tree} renderOptions={rendererProps}/>;
    
    const enableSectionUpdate = useRef(true);
    const timerRef = useRef(null);
    
    useEffect(() => () => {
        // @ts-ignore
        clearTimeout(timerRef.current);
    }, []);
    
    const headingRefs = useRef([]);
    
    useEffect(() => {
        if (hierarchy !== null) {
            const result: HeadingRef[] = [];
            getHeadingRefs(result, hierarchy);
            // @ts-ignore
            headingRefs.current = result;
            
            // console.log(result);
        }
    }, [hierarchy]);
    
    
    const doUpdateScroll = () => {
        if (getIsVerticalMobile()) {
            setViewingSectionHash('');
            return;
        }
        
        if (!scrollDomRef.current) {
            return;
        }
        
        if (!enableSectionUpdate.current) {
            return;
        }
        
        // console.log("scroll updating")
        
        let active: HeadingRef | null = null;
        
        // @ts-ignore
        const scrollTop: number = scrollDomRef.current.scrollTop;
        // @ts-ignore
        const scrollHeight: number = scrollDomRef.current.scrollHeight;
        
        var screenHeight = document.documentElement.clientHeight;
        
        const scrollPosition = ((scrollTop / (scrollHeight - screenHeight) * scrollHeight))
        
        for (let i = headingRefs.current.length - 1; i >= 0; i -= 1) {
            
            const item: HeadingRef = headingRefs.current[i];
            
            if (item.element &&
                item.element.offsetTop <
                scrollPosition + screenHeight / 4
            ) {
                active = item;
                break;
            }
        }
        
        if (active !== null && viewingSectionHash !== active.headingHash) {
            setViewingSectionHash(active.headingHash);
        }
    };
    
    const updateScrollMemoed = useCallback(doUpdateScroll, [viewingSectionHash]);
    
    const titles = (hierarchy !== null && rightBarEnabled) ? <List dense={true} style={{
        fontSize: 16
    }}>
        {hierarchy.map((v, index) => {
            if (!isHeadingWithContent(v)) {
                return undefined;
            }
            
            return <MemoedRightBarSection
                outerSelectedHash={viewingSectionHash}
                item={v}
                layer={0}
                key={index}
                setOuterSelected={null}
                onTitleClick={() => {
                    enableSectionUpdate.current = false;
                    // @ts-ignore
                    timerRef.current = setTimeout(() => {
                        enableSectionUpdate.current = true;
                        updateScrollMemoed();
                    }, 2000);
                }}
                onViewed={(termDom) => {
                    if (rightBarScrollRef.current) {
                        const termY = termDom.getBoundingClientRect().y;
                        
                        if (termY > 100 && ((termY + 200) < window.outerHeight)) {
                            //visible
                        } else {
                            //invisible
                            const scrollClientTop = rightBarScrollRef.current?.getBoundingClientRect().y;
                            const scrollTop = rightBarScrollRef.current?.scrollTop || 0;
                            const offset = termY - scrollClientTop + scrollTop;
                            
                            rightBarScrollRef.current?.scrollTo({top: offset - 200, behavior: 'smooth'});
                        }
                    }
                }}
            />;
        })}
    </List> : <></>;
    
    let location = useLocation();
    
    return {
        wikiContentComponent: <div>
            {/*<Collapse in={tree!==null} timeout={duration}>*/}
            {/*    {wikiContents}*/}
            {/*</Collapse>*/}
            <MyClippingCollapse shown={tree !== null} duration={1000}>
                {wikiContents}
            </MyClippingCollapse>
            {errorLoading && <Typography>
                {'error loading ' + contentUrl}
            </Typography>}
            {(tree === null && !errorLoading) &&
            <CircularProgress/>
            }
        </div>,
        rightBarComponent: titles,
        updateScrollFunc: updateScrollMemoed
    };
}

