import React, {MutableRefObject, useContext, useEffect, useRef} from "react";
import {useHistory, useLocation} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import LinkIcon from "@material-ui/icons/Link";

export const HeadingContext = React.createContext({
    isMatureRef: {current: true}
})

export function getHeadingFontSize(level: number) {
    let fontSize = 55;
    
    switch (level) {
        case 1:
            fontSize = 36
            break
        case 2:
            fontSize = 32
            break
        case 3:
            fontSize = 27
            break
        case 4:
            fontSize = 22
            break
        case 5:
            fontSize = 20
            break
    }
    return fontSize;
}

export function getHeadingColor(level: number) {
    let background = "#ed4b82";
    switch (level) {
        case 1:
            background = "#4dabf5"
            break
        case 2:
            background = "#00a152"
            break
        case 3:
            background = "#ffac33"
            break
        case 4:
            background = "#ed4b82"
            break
        case 5:
            background = "#4dabf5"
            break
    }
    return background;
}

export const headingSelectedColor = '#9c27b020';

export function WikiHeading(props: { level: number, children: any, node: any }) {
    const myRef: React.MutableRefObject<any> = useRef()
    const location = useLocation()
    const history = useHistory()
    
    const headingHash = props.node.myHeadingHash;
    
    const context = useContext(HeadingContext);
    
    const isMatureRef = context.isMatureRef;
    
    const selectedByUrl = location.hash === headingHash;
    
    useEffect(() => {
        if (location.hash) {
            const scrollFunc = () => {
                if (myRef.current) {
                    const y = myRef.current.getBoundingClientRect().y;
                    if (y < 100 || y > (window.innerHeight || window.outerHeight)) {
                        myRef.current.scrollIntoView({behavior: 'smooth'});
                    }
                }
            };
            
            // console.log(headingHash)
            if (selectedByUrl) {
                if (isMatureRef.current) {
                    scrollFunc()
                } else {
                    let timer = setTimeout(() => {
                        scrollFunc();
                    }, 1000);
                    
                    return () => {
                        clearTimeout(timer);
                    }
                }
            }
        }
    }, [headingHash, location.hash])
    
    const level = props.level;
    const fontSize = getHeadingFontSize(level);
    const background = getHeadingColor(level);
    
    // console.log('what');
    
    return <div id={headingHash} style={{
        display: "flex", flexDirection: "row", alignItems: "stretch",
        position: 'relative'
    }} onClick={(e) => {
        history.push(headingHash);
    }}>
        <div style={{
            position: 'absolute',
            left: selectedByUrl ? 0 : '100%', right: 0, top: 0, bottom: 0,
            transition: "left 1000ms",
            background: headingSelectedColor,
            zIndex: -1
        }}/>
        
        <div style={{
            display: "inline-flex", width: 5, background: background,
            marginTop: 4, marginBottom: 4, marginRight: 10,
            flexShrink: 0
        }}/>
        <Typography ref={myRef} style={{fontSize: fontSize}}>
            {props.children}
            {/*<IconButton onClick={() => {*/}
            {/*    history.push(headingHash)*/}
            {/*}}>*/}
            {/*    <LinkIcon/>*/}
            {/*</IconButton>*/}
        </Typography>
    
    
    </div>;
}