import React, {Suspense, useEffect, useRef, useState} from 'react';
import {Route, Switch, useLocation} from "react-router-dom";
import {CSSTransition, TransitionGroup} from "react-transition-group";
import {IndexPage} from "./pages/IndexPage";
import {IPPage} from "./pages/IPPage";
import './App.css'
import {IPWikiPage} from "./pages/wiki/WikiPage";
import {MyBanner} from "./pages/MyBanner";
import {Collapse} from "@material-ui/core";
import Button from "@material-ui/core/Button";

export function TestPage() {
    const [opened, setOpened] = useState(false);
    
    return <div className='My-Page'>
        <MyBanner text={"test"} color={'black'} pageIconPath={''}/>
        
        <Collapse in={true}>
            <p>aaa</p>
            {opened&&<p>bbb</p>}
        </Collapse>
        
        <Button onClick={()=>setOpened(!opened)}>h</Button>
    </div>
}

export function AnimatedPages() {
    let location = useLocation();
    
    let refMouseX = useRef(0);
    let refMouseY = useRef(0);
    
    useEffect(() => {
        let root = document.documentElement;
        
        let listener = (e: any) => {
            refMouseX.current = e.clientX;
            refMouseY.current = e.clientY;
            // root.style.setProperty('--mouse-x', e.clientX + "px");
            // root.style.setProperty('--mouse-y', e.clientY + "px");
        };
        root.addEventListener("mousemove", listener);
        
        return () => {
            root.removeEventListener("mousemove", listener);
        }
    }, []);
    
    document.documentElement.style.setProperty('--mouse-x', refMouseX.current + 'px');
    document.documentElement.style.setProperty('--mouse-y', refMouseY.current + 'px');
    
    return (
        <Suspense fallback={'loading'}>
            <TransitionGroup>
                <CSSTransition
                    key={location.pathname}
                    classNames="fade"
                    timeout={1000}
                >
                    <Switch location={location}>
                        <Route path='/' exact>
                            <IndexPage/>
                        </Route>
                        <Route path='/immptl/' exact>
                            <IPPage/>
                        </Route>
                        <Route path='/immptl/wiki/'>
                            <IPWikiPage/>
                        </Route>
                        <Route path='/test'>
                            <TestPage/>
                        </Route>
                    </Switch>
                </CSSTransition>
            </TransitionGroup>
        </Suspense>
    
    
    );
}