import {Button, Collapse} from "@material-ui/core";
import React, {DOMElement, useEffect, useRef, useState} from "react";
import Axios from "axios";
import i18next from "i18next";
import ReactMarkdown from "react-markdown";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";
import Typography from "@material-ui/core/Typography";
import throttle from 'lodash/throttle';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from "@material-ui/core/Slide";
import axiosRetry from "axios-retry";
import {useTranslation} from "react-i18next";

export function MyButton(props: { children: any, style?: React.CSSProperties, onClick?: any, href?: any }) {
    return <Button style={{
        textTransform: 'none', ...props.style
    }} variant={"contained"} onClick={props.onClick} href={props.href}>{props.children}</Button>;
}

export function useEndPoint(url: string) {
    const [res, setRes] = useState({
        data: null,
        complete: false,
        pending: false,
        error: false
    });
    useEffect(
        () => {
            let unmounted = false;
            
            setRes({
                data: null,
                pending: true,
                error: false,
                complete: false
            });
            Axios({url: url})
                .then(res => {
                    if (unmounted) return;
                    setRes({
                        data: res.data,
                        pending: false,
                        error: false,
                        complete: true
                    });
                },)
                .catch(() => {
                    if (unmounted) return;
                    setRes({
                        data: null,
                        pending: false,
                        error: true,
                        complete: true
                    });
                });
            
            return () => {
                unmounted = true;
            }
        },
        [url]
    );
    return res;
}

export function getCurrentLanguage() {
    var currLanguage = i18next.language;
    
    if (currLanguage === undefined) {
        currLanguage = 'en';
    }
    return currLanguage;
}

export function notEmpty<TValue>(value: TValue | null | undefined): value is TValue {
    return value !== null && value !== undefined;
}

export function swapInArray<TValue>(array: TValue[], i1: number, i2: number) {
    var temp = array[i1];
    array[i1] = array[i2];
    array[i2] = temp;
}

const gfm = require('remark-gfm')

export function MyImage(props: { url: string }) {
    // return <img src={props.url} style={{
    //     maxWidth: '100%'
    // }} alt={'Fails to load ' + props.url}/>
    
    const {t, i18n} = useTranslation();
    
    const [loaded, setLoaded] = useState(false);
    
    return <div style={{position: "relative"}}>
        {!loaded && <div style={{position: "absolute"}}>
            <CircularProgress/>
            <p>{t('image_loading')}</p>
        </div>}
        
        <img src={props.url} onLoad={() => setLoaded(true)} style={{
            maxWidth: '100%', minHeight: 100
        }} alt={'Fails to load ' + props.url} onError={() => setLoaded(true)}/>
        
        
        {/*<Collapse in={!loaded}>*/}
        {/*    <CircularProgress/>*/}
        {/*</Collapse>*/}
        {/*<Collapse in={loaded}>*/}
        {/*    <img src={props.url} onLoad={() => setLoaded(true)} style={{*/}
        {/*        maxWidth: '100%'*/}
        {/*    }} alt={'Fails to load ' + props.url} onError={() => setLoaded(true)}/>*/}
        {/*</Collapse>*/}
    </div>;
}

export const baseMarkdownRenderers = {
    image: (a: any) => {
        return <MyImage url={a.src}/>
    },
    paragraph: (a: any) => {
        return <div style={{marginTop: 10, marginBottom: 10}}>{a.children}</div>
    },
    heading: (a: any) => {
        // console.log(a)
        const level: any = a.level;
        
        return <Typography style={{fontSize: 40 - level * 5}}>{a.children}</Typography>
    }
}

export function MyMarkdown(props: { children: any }) {
    return <ReactMarkdown className='My-MD' plugins={[gfm]} renderers={baseMarkdownRenderers}>
        {props.children}
    </ReactMarkdown>
}

export function MyRemoteMarkdown(props: { url: string }) {
    const {data: resultData, complete: resultComplete} = useEndPoint(props.url);
    
    return resultComplete ? (<MyMarkdown children={resultData || 'error loading ' + props.url}/>)
        : (<CircularProgress/>)
}

export function getStringAfter(a: string, b: string) {
    var i = a.lastIndexOf(b);
    if (i === -1) {
        return a;
    }
    return a.substr(i + b.length);
}

export function getIsVerticalMobile() {
    // return true
    return window.innerHeight > window.innerWidth;
}

export function BannerHideOnScroll(props: {
    window?: () => Window;
    children: React.ReactElement;
}) {
    const {children, window} = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({target: window ? window() : undefined});
    
    return (
        <Slide appear={false} direction="down" in={!trigger}>
            {children}
        </Slide>
    );
}

export function getPublicResourcePath(path: string) {
    return process.env.PUBLIC_URL + path;
}

export function getStringBetween(str: string, before: string, after: string) {
    let i = str.indexOf(before);
    
    if (i === -1) {
        return ''
    }
    
    let j = str.indexOf(after, i + before.length);
    
    if (j === -1) {
        return '';
    }
    
    return str.substr(i + before.length, j - i - before.length);
}

