import React, {DOMElement, useEffect, useRef, useState} from "react";
import {MyBanner} from "../MyBanner";
import {CircularProgress, List} from "@material-ui/core";
import ReactMarkdown from "react-markdown";
import {
    getIsVerticalMobile,
    getPublicResourcePath,
    getStringAfter,
    baseMarkdownRenderers,
    useEndPoint
} from "../../my_util/Util";
import {useTranslation} from "react-i18next";
import {useLocation} from "react-router-dom";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Divider from "@material-ui/core/Divider";
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from "@material-ui/core/Drawer";
import {myParseMarkdownAsync, useWikiContent} from "./WikiContent";
import {
    getLocalWikiUrlFromGithubWikiUrl,
    MyLinkInWiki, MySidebarLink,
    WikiToSideBarContext
} from "./WikiSidebar";
import {WikiHeading} from "./WikiHeading";
import DetailsIcon from '@material-ui/icons/Details';
import DescriptionIcon from "@material-ui/icons/Description";

const gfm = require('remark-gfm')

//both github wiki url and this website url
function getMdUrlFromWikiUrl(url: string, head: string) {
    var innerPath = getStringAfter(url, '/wiki');
    
    if (innerPath.length === 0) {
        return head + '/Home.md';
    }
    
    return head + innerPath + '.md';
}

export function removeAssignedHash(text: string) {
    let i = text.indexOf('{#');
    if (i !== -1) {
        return text.slice(0, i);
    }
    
    return text;
}

const wikiMarkdownRenderers = {
    ...baseMarkdownRenderers,
    text: (a: any) => {
        let text: string = a.value;
        return removeAssignedHash(text);
    },
    table: (a: any) => {
        return <Table>{a.children}</Table>
    },
    tableHead: (a: any) => <TableHead>{a.children}</TableHead>,
    tableRow: (a: any) => <TableRow>{a.children}</TableRow>,
    tableBody: (a: any) => <TableBody>{a.children}</TableBody>,
    tableCell: (a: any) => <TableCell>{a.children}</TableCell>,
    link: (a: any) => {
        return <MyLinkInWiki targetLink={a.href}>{a.children}</MyLinkInWiki>
    },
    heading: (a: any) => {
        return <WikiHeading level={a.level} node={a.node}>{a.children}</WikiHeading>
    },
    code: (a: any) => {
        return <pre style={{
            margin: 5, padding: 5,
            overflowX: 'auto',
            background: '#e4e4e4'
        }}>
            <code>{a.value}</code>
        </pre>
    },
    inlineCode: (a: any) => {
        return <code style={{
            background: '#e4e4e4'
        }}>{a.value}</code>
    }
}

interface WikiPageEntry {
    title: string,
    url: string
}

function parseWikiPageEntries(result: Array<WikiPageEntry>, node: any) {
    if (node.type === 'link') {
        let newEntry: WikiPageEntry = {
            title: node.children[0].value,
            url: node.url
        };
        
        result.push(newEntry);
    }
    
    if (node.children) {
        for (let c of node.children) {
            parseWikiPageEntries(result, c);
        }
    }
    
    return null;
}

export function IPWikiPage() {
    let {t, i18n} = useTranslation();
    
    let location = useLocation();
    
    const wikiContentUrl = getMdUrlFromWikiUrl(location.pathname, t('url_wiki_content_prefix'));
    const fallbackWikiContentUrl = getMdUrlFromWikiUrl(location.pathname, "https://qouteall.fun/imm_ptl_wiki_copy");
    
    const [wikiTitle, setWikiTitle] = useState('Loading...');
    
    const [wikiPageEntries, setWikiPageEntries] = useState([] as WikiPageEntry[]);
    
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    
    const [isPageNotFound, setIsPageNotFound] = useState(false);
    
    const sidebarUrl = t('url_wiki_sidebar');
    const {data: sideBarData, complete: sidebarComplete} = useEndPoint(sidebarUrl);
    
    useEffect(() => {
        async function doParseLeftbar() {
            if (sideBarData === null) {
                return;
            }
            
            const node = await myParseMarkdownAsync({children: sideBarData as unknown as string});
            
            let newPageEntries: Array<WikiPageEntry> = [];
            parseWikiPageEntries(newPageEntries, node);
            
            setWikiPageEntries(newPageEntries);
            
            let result = newPageEntries.find(entry =>
                getLocalWikiUrlFromGithubWikiUrl(entry.url) === location.pathname
            );
            
            if (result) {
                setIsPageNotFound(false);
                setWikiTitle(result.title);
            } else {
                setIsPageNotFound(true);
            }
        }
        
        doParseLeftbar();
    }, [location.pathname, sidebarComplete]);
    
    const leftBarComponent = <WikiToSideBarContext.Provider value={{
        onTermClicked: () => setIsDrawerOpen(false)
    }}>
        <Typography variant='h5' style={{margin: 10, marginBottom: 0, marginLeft: 15}}>
            {t('Pages')}
        </Typography>
        <Divider style={{margin: 10}}/>
        
        <List component='nav' style={{maxWidth: '80vw'}}>
            {
                sidebarComplete ?
                    wikiPageEntries.map(entry =>
                        <MySidebarLink url={entry.url} children={entry.title} key={entry.url}/>
                    )
                    : <CircularProgress/>
            }
        </List>
    </WikiToSideBarContext.Provider>;
    
    const leftBarOnMobile = <>
        <Drawer anchor='left' open={isDrawerOpen} onClose={
            () => setIsDrawerOpen(false)
        } style={{}}>
            {leftBarComponent}
        </Drawer>
        
        {/*render to make the effect trigger and set the wiki title.
         It's a workaround and will be refactored*/}
        {!isDrawerOpen && <div style={{display: "none"}}>
            {leftBarComponent}
        </div>}
    </>;
    
    const leftBarOnPC = <div style={{
        // position: 'sticky', top: 0,
        minWidth: '20vw', maxWidth: '20vw'
    }} className='My-Sticky'>{leftBarComponent}</div>;
    
    const scrollDomRef = useRef(null);
    
    const rightBarScrollRef = useRef(null as (null | HTMLDivElement));
    
    const {
        wikiContentComponent: wikiContentComponent,
        rightBarComponent: rightBarComponent,
        updateScrollFunc: updateScrollFunc
    } = useWikiContent(
        // @ts-ignore
        wikiContentUrl, wikiMarkdownRenderers, [gfm], scrollDomRef,
        fallbackWikiContentUrl,
        rightBarScrollRef
    );
    
    const scrollTimerRef = useRef(null as any);
    
    const isVerticalMobile = getIsVerticalMobile();
    return <div className='My-Page' style={{
        display: 'flex', flexDirection: 'column'
    }}>
        <MyBanner text={t('Immersive Portals Wiki')} color='#9500ae'
                  pageIconPath={getPublicResourcePath('/immptl.png')}>
            {isVerticalMobile &&
            <IconButton onClick={() => setIsDrawerOpen(!isDrawerOpen)}>
                <MenuIcon style={{color: 'white'}}/>
            </IconButton>}
        </MyBanner>
        
        <div style={{
            display: 'flex', flexDirection: 'row', alignItems: "flex-start",
            overflowY: 'auto',
            minHeight: 0, flexBasis: 0, flexGrow: 1
        }} onScroll={() => {
            if (scrollTimerRef.current) {
                clearTimeout(scrollTimerRef.current);
            }
            
            scrollTimerRef.current = setTimeout(updateScrollFunc, 300);
        }} ref={scrollDomRef}>
            {isVerticalMobile ? leftBarOnMobile : leftBarOnPC}
            
            <div style={{
                margin: 0, padding: 20, flexGrow: 1,
                minWidth: 0, flexBasis: 0
            }} ref={rightBarScrollRef}>
                <Typography variant='h4'>
                    {wikiTitle}
                </Typography>
                <Divider style={{margin: 10}}/>
                {isPageNotFound ? 'Page Not Found' : wikiContentComponent}
            </div>
            
            {!isVerticalMobile && <div ref={rightBarScrollRef} style={{
                margin: 0, minWidth: '20vw', maxWidth: '20vw',
                alignSelf: 'stretch', overflowY: 'auto',
            }} className='My-Sticky'>
                <Typography variant='h5' style={{margin: 10, marginBottom: 0}}>
                    {t('Contents')}
                </Typography>
                <Divider style={{margin: 10}}/>
                {rightBarComponent}
            </div>}
        
        
        </div>
    
    </div>;
}