import React, {CSSProperties, useEffect, useRef, useState} from "react";

import {Transition} from 'react-transition-group';
import {Button} from "@material-ui/core";

export const MyClippingCollapse = (props: { shown: boolean, children: any, duration: number }) => {
    const [state, setState] = useState(0);
    
    const domRef = useRef(null as null | HTMLDivElement);
    
    let clipPath: string = '';
    if (state === 0) {
        clipPath = `inset(0% 0% 100% 0%)`;
    } else if (state === 1) {
        let contentHeight = domRef.current?.clientHeight;
        
        if (!contentHeight) {
            contentHeight = 100;
        }
        
        let num = contentHeight - window.innerHeight;
        
        // console.log(num);
        
        if (num < 0) {
            num = 0;
        }
        
        clipPath = `inset(0px 0px ${num}px 0px)`;
    } else if (state === 2) {
        clipPath = 'unset';
    }
    
    useEffect(() => {
        if (props.shown) {
            setState(1);
            setTimeout(() => {
                setState(2);
            }, props.duration);
        }
    }, [props.shown]);
    
    return <div ref={domRef} style={{
        transition: `clip-path ${props.duration}ms ease-in`,
        clipPath: clipPath
    }}>
        {props.children}
    </div>;
};

