const roundStyle = {
    entering: {clipPath: 'circle(100%)',},
    entered: {clipPath: 'circle(100%)',},
    exiting: {clipPath: 'circle(0%)',},
    exited: {clipPath: 'circle(0%)',},
};

export const MyRoundAnimation = (props: { shown: boolean, children: any, duration: number }) => {
    const defaultStyle = {
        transition: `clip-path ${props.duration}ms ease-in`,
    }
    
    return (
        <Transition in={props.shown} timeout={props.duration}>
            {state => {
                // console.log(state)
                return (
                    <div style={{
                        ...defaultStyle,
                        // @ts-ignore
                        ...roundStyle[state]
                    }}>
                        {props.children}
                    </div>
                );
            }}
        </Transition>
    );
};