import {Link, useHistory, useLocation} from "react-router-dom";
import React, {useContext, useEffect, useState} from "react";
import {ListItem} from "@material-ui/core";
import {MyButton} from "../../my_util/Util";
import {useTranslation} from "react-i18next";
import DetailsIcon from "@material-ui/icons/Details";
import DescriptionIcon from '@material-ui/icons/Description';
import BookIcon from '@material-ui/icons/Book';

export const WikiToSideBarContext = React.createContext({
    onTermClicked: () => {
    }
});

function processLink(url: string): string {
    return url.substr(url.indexOf('/wiki'))
}

export function getLocalWikiUrlFromGithubWikiUrl(githubWikiUrl: string) {
    const processed = processLink(githubWikiUrl);
    return '/immptl' + processed;
}

